export const email = {
  rules: [
    {
      test: /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/i,
      message: "Informe um e-mail válido."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};

export const celular = {
  rules: [
    {
      test: /^(?:\+)[0-9]{2}\s?(?:\()[0-9]{2}(?:\))\s?[0-9]{4,5}(?:-)[0-9]{4}$/i,
      message: "Informe um número válido."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};

export const url = {
  rules: [
    {
      test: /^(ftp|http|https):\/\/[^ "]+$/i,
      message: "Informe uma url válida."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};
export const cnpj = {
  rules: [
    {
      test: /[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2}/g,
      message: "Informe um CNPJ válido."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};
export const password = {
  rules: [
    {
      test: /^(?=\w{5})(?=.*\d{2,})/i,
      message: "A senha precisa ser maior do que 6 caractéres."
    },
    {
      test: /^[a-z0-9A-Z_]+$/,
      message: "Informe uma senha válida."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};
export const username = {
  rules: [
    {
      test: /^[a-zA-Z_]+$/i,
      message: "Números não são permitidos."
    }
  ],
  errors: [],
  valid: false,
  state: ""
};



