import React, { Component } from "react";
import {
  Button,
  Row,
  Col,  
  Container
} from "reactstrap";
import img from "../../assets/images/logo-white.svg";
import Validator from "./validator";
import sucesso from "../../assets/images/icon/sucesso.svg";
export class queueDone extends Component {
  constructor(props) {
    super(props);
    this.validators = Validator;
    this.nextStep = this.props.nextStep;
  }
  handleClick = e => {
    e.preventDefault();
    this.nextStep();
  };

  render() {
    return (
      <Container className="QueueDone h-100">
        <Row noGutters className="">
          <Col className="text-center">
            <img src={sucesso} alt="" className="img-fluid sucesso" srcSet="" />
          </Col>
        </Row>

        <Row noGutters className="justify-content-center my-2">
          <Col>
            <h1 className="text-center title">Parabéns!</h1>
          </Col>
        </Row>

        <Row noGutters className="justify-content-center">
          <Col className="text-center description">
            Você está em nossa lista de espera Banky, e em breve entraremos em
            contato.
          </Col>
        </Row>
        <Row noGutters className="mb-auto justify-content-center">
          <Col>
            <Button
              size="lg"
              block
              type="button"
              onClick={this.handleClick.bind(this)}
              className="btn btn-outline-light"
            >
              Voltar para a home
            </Button>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default queueDone;



